.container-desk {
  padding-left: 250px;
  padding-right: 250px; }

@font-face {
  font-family: 'Roboto Condensed';
  font-display: swap;
  src: url("RobotoCondensed-Bold.woff2") format("woff2"), url("RobotoCondensed-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: 'Roboto Condensed';
  font-display: swap;
  src: url("RobotoCondensed-Light.woff2") format("woff2"), url("RobotoCondensed-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: 'Roboto Condensed';
  font-display: swap;
  src: url("RobotoCondensed-Regular.woff2") format("woff2"), url("RobotoCondensed-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'PT Mono';
  font-display: swap;
  src: url("PTMono-Regular.woff2") format("woff2"), url("PTMono-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  font-family: 'PT Mono', "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  color: #666666; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
